html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, main, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, main {
  display: block; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
body {
  height: 100%; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

/* Helper classes
----------------------------------------- */
.ir {
  background-color: transparent;
  border: 0;
  overflow: hidden;
  *text-indent: -9999px; }

.ir:before {
  content: "";
  display: block;
  width: 0;
  height: 150%; }

.hidden {
  display: none !important;
  visibility: hidden; }

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto; }

.invisible {
  visibility: hidden; }

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table; }

.clearfix:after {
  clear: both; }

.clearfix {
  *zoom: 1; }

/* Print Styles
----------------------------------------- */
@media print {
  * {
    background: transparent !important;
    color: #000 !important;
    /* Black prints faster: h5bp.com/s */
    box-shadow: none !important;
    text-shadow: none !important; }
  a,
  a:visited {
    text-decoration: underline; }
  a[href]:after {
    content: " (" attr(href) ")"; }
  abbr[title]:after {
    content: " (" attr(title) ")"; }
  /*
     * Don't show links for images, or javascript/internal links
     */
  .ir a:after,
  a[href^="javascript:"]:after,
  a[href^="#"]:after {
    content: ""; }
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid; }
  thead {
    display: table-header-group;
    /* h5bp.com/t */ }
  tr,
  img {
    page-break-inside: avoid; }
  img {
    max-width: 100% !important; }
  @page {
    margin: 0.5cm; }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3; }
  h2,
  h3 {
    page-break-after: avoid; } }

/* ============================================
	S I M P L E - G R I D

	Dead simple grids. Cross browser. Semantic. Intuitive. Follows Simple's philosophy of SOMA.

	Browsers: ie8, ie10, ie11, FF, Safari, Chrome

	Dependancies :

		Variables below defined in your _base.scss or similar.

			$container-class: 'foo';
			$row-class: 'foo';
			$column-class: 'bar';
			$gutter: 3%; // spacing between columns
			$max-columns: 6; //  @for loop class declarations - used for equal column single digit
			breakpoint mixin function - included in repo.

	Usage :

		.columns-{percentage, double digit} for mixed columns
			.columns-70, .columns-30 = totaling to 100

		.columns-{single digit number} for equal coloumns
			.columns-1
			.columns-2, .columns-2
			.columns-4, .columns-4, .columns-4, .columns-4

============================================ */
[class~="container"],
[data-layout='container'] {
  position: relative;
  overflow: hidden;
  margin-right: auto;
  margin-left: auto;
  padding-left: 1.5%;
  padding-right: 1.5%; }
  [class~="container"]::after,
  [data-layout='container']::after {
    clear: both;
    content: "";
    display: table; }
  @media (min-width: 768px) {
    [class~="container"],
    [data-layout='container'] {
      width: 768px; } }
  @media (min-width: 960px) {
    [class~="container"],
