/* Typography
----------------------------------------- */
*,
*:after,
*:before {
  box-sizing: border-box; }

html,
button,
input,
select,
textarea {
  color: #333; }

::-moz-selection {
  background: #FF4136;
  color: white;
  text-shadow: none; }

::selection {
  background: #FF4136;
  color: white;
  text-shadow: none; }

hr {
  border: none;
  clear: both;
  margin: 44px auto 54px;
  width: 65%;
  border-bottom: 5px solid #f5f5f5;
  display: inline-block;
  left: 50%;
  margin-left: -32.5%;
  position: relative; }

abbr, acronym {
  text-transform: uppercase;
  font-size: 90%;
  color: #222;
  border-bottom: 1px solid #cccccc;
  cursor: help; }

abbr {
  text-transform: none; }

img {
  vertical-align: middle;
  interpolation-mode: bicubic; }

/*
* Flexible Media
*/
img, video, canvas, object, embed {
  max-width: 100%;
  height: auto; }

/*
* Flexible Video iFrame Embed
* use like :
<figure class="aspect">
	<iframe src="https://www.youtube.com/embed/4Fqg43ozz7A"></iframe>
</figure>
*/
figure.aspect {
  height: 0;
  padding-bottom: 56.25%;
  /* 16:9 */
  position: relative;
  overflow: hidden; }
  figure.aspect iframe,
  figure.aspect video,
  figure.aspect embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

textarea {
