  margin: 0 0 1rem;
  border-radius: 2px;
  overflow-x: scroll; }
  pre code {
    overflow-x: scroll;
    border: none;
    word-wrap: initial;
    background-color: transparent; }

code {
  font-size: 12px;
  font-size: 0.75em;
  font-size: 0.75rem;
