  background: #FF4136;
  color: white;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
  line-height: 1.5;
  padding: 1.5rem 0; }
  @media (min-width: 768px) {
    .example-grid [class*="columns-"],
    .example-grid [data-columns] {
      padding: 1.5rem; } }
  .example-grid [class*="columns-"] p,
  .example-grid [data-columns] p {
    font-weight: 400; }
  .example-grid [class*="columns-"] h2,
  .example-grid [data-columns] h2 {
    margin: 0; }

.hero {
  border-bottom: 1px solid #eee; }

article {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #eee; }

input[type="search"] {
  padding: 0.8rem 1rem;
  border: 0; }
  input[type="search"]:focus {
    border: 0; }

.juice-bg {
  background-color: #FF4136;
  color: white; }
  .juice-bg a {
    color: white;
    border-color: white; }
  .juice-bg.example-grid [class*="columns-"],
  .juice-bg.example-grid [data-columns] {
    background: #fff;
    color: #222; }

.circle {
  background: #f5f5f5;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  display: block;
  margin: 0 auto; }

#footer {
  border-top: 1px solid #eee;
  padding: 5rem 0;
  font-weight: 600;
  font-size: 0.8rem;
  text-align: center; }
  #footer .created-by,
  #footer .footer-meta a {
    display: block;
    line-height: 2; }
    @media (min-width: 480px) {
      #footer .created-by,
      #footer .footer-meta a {
        display: inline; } }
  @media (min-width: 480px) {
    #footer .created-by {
      text-align: left;
      box-sizing: border-box;
      position: relative;
      min-height: 1px;
      width: 100%;
      width: 48.5%;
      float: left;
      margin-right: 1.5%; }
      .no-gutter #footer .created-by {
        width: 50%; }
      .no-gutter #footer .created-by {
        margin-right: 0; }
    #footer .footer-meta {
      text-align: right;
      box-sizing: border-box;
      position: relative;
      min-height: 1px;
      width: 100%;
      width: 48.5%;
      float: left;
      margin-right: 1.5%; }
      .no-gutter #footer .footer-meta {
        width: 50%; }
      .no-gutter #footer .footer-meta {
        margin-right: 0; }
      #footer .footer-meta a {
        margin-left: 1rem; } }

@media (min-width: 768px) {
  .text-columns {
    column-count: 2; } }

@media (min-width: 960px) {
  .has-sidebar .primary {
    box-sizing: border-box;
    position: relative;
    min-height: 1px;
    width: 100%;
    width: 68.5%;
    float: left;
    margin-right: 1.5%; }
    .no-gutter .has-sidebar .primary {
      width: 70%; }
    .no-gutter .has-sidebar .primary {
      margin-right: 0; } }

@media (min-width: 960px) {
  .has-sidebar #sidebar {
    box-sizing: border-box;
    position: relative;
    min-height: 1px;
    width: 100%;
    width: 28.5%;
    float: left;
    margin-right: 1.5%; }
    .no-gutter .has-sidebar #sidebar {
      width: 30%; }
    .no-gutter .has-sidebar #sidebar {
      margin-right: 0; } }

.float-left {
  float: left; }

.float-right {
  float: right; }

