Taken from http://csswizardry.com/2011/10/the-island-object/) -- */
@media (min-width: 768px) {
  .island {
    padding: 3rem 5rem; } }

@media (min-width: 960px) {
  .island {
    padding: 3rem 7rem; } }

@media (min-width: 1280px) {
  .island {
    padding: 3rem 15rem; } }
